input {
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: 12px;
    padding-right: 108px;
    box-sizing: border-box;
    border-radius: 8px;
    outline: none;
    border: 1px solid #333333;
}
.default_hover:hover {
    border: 2px solid #333333;
    outline: none;
}
.default_hover:hover + label {
    color: #333333;
}
.default_hover:focus {
    border: 2px solid #2962FF;
    outline: none;
}
.default_hover:focus + label {
    color: #2962FF; 
}

.error {
    border: 1px solid #D32F2F;
    outline: none;
}
.error + label {
    color: #D32F2F;
}
.error:hover {
    border: 2px solid #333333;
    outline: none;
}
.error:hover + label {
    color: #333333;
}
.error:focus {
    border: 2px solid #D32F2F;
    outline: none;
}
.error:focus + label {
    color: #D32F2F;
}
.disabled {
    background: #F2F2F2;
    border: 1px solid #E0E0E0;
}
/* .helperText {
    border: 1px solid #828282;
    outline: none;
} */
.helperText_error {
    display: block;
    border: none;
    color: #D32F2F;
}

.fillWidth {
    width: -webkit-fill-available;
}
p {
    margin-top: 3rem;
    margin-bottom: 5px;
}